	module.exports = {"data":{"markdownRemark":{"html":"<p>_ Преобразование Лапласа функции f(t) имеет вид:\n_ L(s) = f(t)e-stdt.\n_ Принято f(t) назывть оригиналом, L(s) – изображением.</p>\n<p>[cut]</p>\n<p>_ Алгоритм, или точнее методика, примеры ниже:</p>\n<ul>\n<li>выполняется пробразование Лапласа, тем самым от ДУ переходим к алгебраическому уравнению для изображений</li>\n<li>в символьном виде выполняется поиск решения полученного уравнения</li>\n<li>для полученного решения выполняется обратное преобразование Лапласа</li>\n</ul>\n<p>_ Глоссарий:\n_ изображение функции - для линейных дифференциальных уравнений существует изоморфизм (взаимно-однозначное соответствие) между функциями-оригиналами, входящими в уравнение и их изображениями (образами при преобразовании Лапласа).</p>\n<p>_ Ссылки:</p>\n<ul>\n<li><a href=\"http://www.exponenta.ru/educat/systemat/kasakov/theme3/pr_du3.htm\">Примеры решения в Mathcad</a></li>\n<li><a href=\"http://xplusy.isnet.ru/Files/Files_diff_ur/Operatornim_metodom.pdf\">Примеры решения Maple</a></li>\n<li><a href=\"http://matlab.exponenta.ru/forum/post906.html\">Примеры решения Matlab</a></li>\n<li><a href=\"http://xplusy.isnet.ru/\">Много полезных ссылок для по математике для студентов</a></li>\n</ul>","frontmatter":{"date":"2015-10-04","path":"/page/reshenie-differencialnyh-uravnenij-operatornym-metodom","title":"Решение дифференциальных уравнений операторным методом"}}},"pathContext":{}}

